"use client"

import { Header } from "@/components/layout/header"
import { mockCompetitions } from "@/lib/mock-competitions"
import { useParams, useRouter } from "next/navigation"
import { useState } from "react"

export default function CompetitionEditPage() {
  const params = useParams()
  const router = useRouter()
  const competitionId = params.id as string
  const competition = mockCompetitions.find((comp) => comp.id === competitionId)

  if (!competition) {
    return (
      <div className="min-h-screen bg-gray-50">
        <Header />
        <div className="max-w-4xl mx-auto px-6 py-12">
          <div className="text-center">
            <h1 className="text-2xl font-semibold mb-2">Соревнование не найдено</h1>
            <p className="text-gray-600 mb-6">Соревнование с указанным ID не существует.</p>
            <button
              onClick={() => router.push("/competitions")}
              className="text-blue-600 hover:text-blue-700 font-medium"
            >
              Вернуться к соревнованиям
            </button>
          </div>
        </div>
      </div>
    )
  }

  const handleCancel = () => {
    router.push(`/competitions/${competitionId}`)
  }

  return (
    <div className="min-h-screen bg-gray-50">
      <Header />
      <div className="max-w-[896px] mx-auto px-8 py-6">
        {/* TODO: Implement competition edit form based on Figma design */}
        <div className="bg-white border border-gray-200 rounded-[10px] p-6">
          <h1 className="text-xl font-semibold mb-4">Редактирование соревнования</h1>
          <p className="text-gray-600 mb-6">
            Форма редактирования соревнования будет реализована здесь
          </p>
          <div className="flex gap-4">
            <button
              onClick={handleCancel}
              className="px-4 py-2 border border-gray-200 rounded-lg hover:bg-gray-50"
            >
              Отменить
            </button>
            <button
              onClick={handleCancel}
              className="px-4 py-2 bg-blue-500 text-white rounded-lg hover:bg-blue-600"
            >
              Сохранить
            </button>
          </div>
        </div>
      </div>
    </div>
  )
}

